h1, p {
  color: hsl(229, 6%, 66%);
}

h2, h3 {
  color: hsl(234, 12%, 34%);
}

body {
  background-color: hsl(0, 0%, 98%);
}

main article {
  background-color: #fff;
  box-shadow: 4px 6.5px 8px 2px rgba(0, 0, 0, 0.15);
}
main article:nth-child(1) {
  border-top: 4px solid hsl(180, 62%, 55%);
}
main article:nth-child(2) {
  border-top: 4px solid hsl(0, 78%, 62%);
}
main article:nth-child(3) {
  border-top: 4px solid hsl(34, 97%, 64%);
}
main article:nth-child(4) {
  border-top: 4px solid hsl(212, 86%, 64%);
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

@font-face {
  font-family: "pop-light";
  src: url(../../../assets/fonts/poppins-extralight-webfont.woff) format("woff"), url(../../../assets/fonts/poppins-extralight-webfont.woff2) format("woff2");
  font-weight: 200;
  font-style: normal;
}
@font-face {
  font-family: "pop-regular";
  src: url(../../../assets/fonts/poppins-regular-webfont.woff) format("woff"), url(../../../assets/fonts/poppins-regular-webfont.woff2) format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "pop-dark";
  src: url(../../../assets/fonts/poppins-semibold-webfont.woff) format("woff"), url(../../../assets/fonts/poppins-semibold-webfont.woff2) format("woff2");
  font-weight: 600;
  font-style: normal;
}
html {
  font-size: 15px;
  font-family: "pop-regular";
}

header {
  text-align: center;
  margin-bottom: 3rem;
}
header h1 {
  font-family: "pop-light";
  font-size: 1.6rem;
  margin-bottom: -0.7rem;
}
header h2 {
  font-family: "pop-dark";
  font-size: 1.6rem;
}
header p {
  line-height: 1.5;
}

main article {
  padding: 1rem 1.8rem 1.5rem 1.8rem;
  margin-bottom: 1.7rem;
  border-radius: 0.4rem;
}
main article h3 {
  font-family: "pop-dark";
  margin-bottom: -0.5rem;
}
main article P {
  line-height: 1.7;
  font-size: 0.9rem;
  margin-bottom: 2rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

@media (min-width: 1200px) {
  html {
    font-size: 16px;
  }
}
body {
  display: grid;
  place-items: center;
  padding: 3rem 1.5rem;
  position: relative;
}

header p {
  max-width: 29.3333333333rem;
}
@media (min-width: 800px) {
  header p {
    max-width: 36.6666666667rem;
    font-size: 14.7px;
  }
}

main article {
  display: flex;
  flex-direction: column;
  max-width: 20rem;
}
main article img {
  width: 3.4666666667rem;
  align-self: flex-end;
}
@media (min-width: 800px) {
  main {
    display: grid;
    grid-template-areas: "b1 b2" "b3 b4";
    column-gap: 2rem;
  }
  main article:nth-child(1) {
    grid-area: b2;
  }
  main article:nth-child(2) {
    grid-area: b1;
  }
  main article:nth-child(3) {
    grid-area: b4;
  }
  main article:nth-child(4) {
    grid-area: b3;
  }
}
@media (min-width: 1200px) {
  main {
    display: grid;
    grid-template-areas: ". b1 b1 ." "b2 b1 b1 b3" "b2 b4 b4 b3" ". b4 b4 .";
    column-gap: 2rem;
  }
  main article:nth-child(1) {
    grid-area: b2;
  }
  main article:nth-child(2) {
    grid-area: b1;
  }
  main article:nth-child(3) {
    grid-area: b4;
  }
  main article:nth-child(4) {
    grid-area: b3;
  }
}

footer {
  position: absolute;
  bottom: 0;
  margin-bottom: -4rem;
}